
import { computed, defineComponent } from "vue";
import { useStore } from "vuex";

export default defineComponent({
  name: "App",
  setup() {
    const store = useStore()
    return {
      reverse: () => {
        store.commit("hoge/increment")
      },
      count: computed(() => {
        return store.getters["hoge/count"]
      })
    }
  },
  components: {},
});
